import React, { useState } from 'react'
import { ListGroup, Badge } from 'react-bootstrap'
import moment from 'moment'

const timeFormat = 'HH:mm'
const Note = (props) => {
  function shouldShowPassword() {
    const currentDate = moment()
    const currentTime = moment(new Date(), timeFormat)
    return (
      currentTime.isBetween(
        moment(props.note.from, timeFormat),
        moment(props.note.to, timeFormat)
      ) || currentDate.isAfter(props.note.endDate)
    )
  }

  function onDeleteHandler() {
    props.onDelete(props.note.id)
  }

  function copyPassHandler() {
    navigator.clipboard.writeText(props.note.note)
  }

  return (
    <ListGroup.Item>
      <h5>{props.note.title}</h5>
      <p>
        Opens between {moment(props.note.from, 'hh:mm a').format('hh:mm a')} -{' '}
        {moment(props.note.to, 'hh:mm a').format('hh:mm a')}
      </p>
      <p>
        {'Expires On ' + moment(props.note.endDate).format('MMMM Do, h:mm a')}
      </p>
      {shouldShowPassword() && props.note.note}
      {!shouldShowPassword() && '******************'}
      <div className="deleteBtnContainer">
        {shouldShowPassword() && (
          <Badge onClick={copyPassHandler} className="deleteBadge" bg="primary">
            Copy
          </Badge>
        )}

        <Badge
          onClick={onDeleteHandler}
          className="deleteBadge ml-1"
          bg="secondary"
        >
          Delete
        </Badge>
      </div>
    </ListGroup.Item>
  )
}

export default Note
